.header{
  width: 100%;
  height: 800px;
  background: url('../img/Orison-Hero-5.jpg') top center;
  background-size: cover;

}
.opacity_change{
  background: rgba(33, 33, 33, 0);
}
// mobile menu

.header__mobileMenuBlock{
  width: 100%;
  // background: rgba(33, 33, 33, 0.98);
  height: 70px;
  margin: auto;
  position: fixed;
  padding: 10px 0;
  z-index: 500;


  @include bp-m{
    display: none;
  }


}
.mobileMenuBlock__wrapper{
display: flex;
justify-content: space-between;
align-items: center;
margin: auto;
padding: 0 20px;


}
.mobileMenuBlock__logo{
width: 120px;


img{
  max-width: 150px;
  height: auto;
  padding: 10px 0;
}
}

// burger

.mobileMenuBlock__burger{
  width: 30px;
  cursor: pointer;

  .fa-bars{
    color: #fff;
    font-size: 1.2rem;
  }


}

.mobileMenuBlock__mobileNav{
  height: 350px;
  display: flex;
  flex-flow: column wrap;
  text-align: center;
  justify-content: space-around;
  background: rgba(33, 33, 33, 0.98);
  position: relative;
  top:-800px;
  padding-top: 70px;
  padding-bottom: 20px;

  z-index: -100;


}
.header__mobileNav--item{
  color: #fff;
  &:hover{
    text-decoration: none;
    color:$bluegrey;

  }

}


// desktop menu
.header__desktopMenuBlock{
  display: none;
    position: fixed;
    width: 100%;
    z-index: 200;

  @include bp-m{
    display: block;
    background: rgba(33, 33, 33, 0.98);
    padding: 15px 20px;
  }

}

.header__nav--logo{
width: 120px;


img{
  max-width: 150px;
  height: auto;
  padding: 10px 0;
}
}

.desktopMenu__wrapper{


  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  width: 100%;
}

.header__nav--item{
  color:#fff;
  padding-right: 10px;
  @include transition;
  &:hover{
    text-decoration: none;
    color: $bluegrey
  }

}

.header__btn{


  text-align: right;
}


// headings

.headings{
padding-top: 200px;

  // @include bp-m{
  //   width: 50%;
  // }


}
.headings__wrapper{

  margin:  0;



  h1{
    color: #fff;
    span{
      color:$lightgrey;
      font-size:2.5rem;

      @media screen and (max-width: 480px) {
        font-size:2rem;
        line-height: 0.5rem;
      }
    }
  }
}

.headings__btnContainer{
  padding: 15px 0;
}

.headings__btnContainer .btn--transparent{
  margin-top: 15px;
}
