// ..........................................
// buttons

%btn{

  border-radius:40px;
  padding:10px 40px;
  cursor: pointer;
	text-transform: uppercase;
	display: inline-block;
	letter-spacing: 2px;
	font-size: 0.8rem;
	font-family: 'lineto-akkurat-bold';
	margin-right: 10px;



}


.btn--transparentHidden{
	@extend %btn ;
	color:#fff;

	&:hover{
		border:1px solid $bluegrey;
		text-decoration: none;

		color:$bluegrey;

	}
}
.btn--transparent{
	@extend %btn ;
	color:#fff;
	border:1px solid #fff;
	&:hover{
		text-decoration: none;
		background:#fff;
		color:$browngrey;

	}
}

.btn--transparentBlue{
	@extend %btn ;
	color:#fff;
	border:1px solid #fff;
	&:hover{
		text-decoration: none;
		background:$bluegrey;
		border:1px solid $bluegrey;

	}
}


.btn--opaque{
	@extend %btn ;
	background: $bluegrey;
	color:#fff;
	border:1px solid $bluegrey;

	&:hover{
		text-decoration: none;
		background: #fff;
		color:$browngrey;
		border-color: #fff;

	}

}



.btn--opaqueBorder{
	@extend %btn ;
	background: $bluegrey;
	color:#fff;
	border:1px solid $bluegrey;

	&:hover{
		text-decoration: none;
		background: #fff;
		color:$bluegrey;
		border-color: $bluegrey;

	}

}


