@font-face {
font-family: 'lineto-akkurat-bold';
src: url('../fonts/lineto-akkurat-pro-bold.eot');
src: url('../fonts/lineto-akkurat-pro-bold.eot') format('embedded-opentype');
src: url('../fonts/lineto-akkurat-pro-boldd41d.eot') format('embedded-opentype'),
     url('../fonts/lineto-akkurat-pro-bold.html') format('html'),
     url('../fonts/lineto-akkurat-pro-bold.woff') format('woff');
font-style: normal;
font-weight: bold;
}
@font-face {
font-family: 'lineto-akkurat-light';
src: url('../fonts/lineto-akkurat-pro-light.eot');
src: url('../fonts/lineto-akkurat-pro-light.eot') format('embedded-opentype');
src: url('../fonts/lineto-akkurat-pro-lightd41d.eot') format('embedded-opentype'),
     url('../fonts/lineto-akkurat-pro-light.html') format('html'),
     url('../fonts/lineto-akkurat-pro-light.woff') format('woff');
font-style: normal;
font-weight: lighter;
}
@font-face {
font-family: 'lineto-akkurat-regular';
src: url('../fonts/lineto-akkurat-pro-regular.eot');
src: url('../fonts/lineto-akkurat-pro-regular.eot') format('embedded-opentype');
src: url('../fonts/lineto-akkurat-pro-regulard41d.eot') format('embedded-opentype'),
     url('../fonts/lineto-akkurat-pro-regular.html') format('html'),
     url('../fonts/lineto-akkurat-pro-regular.woff') format('woff');
font-style: normal;
font-weight: lighter;
}

body{
font-family: 'lineto-akkurat-regular';
font-size: 1rem;
}

h1{
  font-family: 'lineto-akkurat-light';
  font-size: 3rem;

  font-weight: normal;
}

.header__nav--item, header__mobileNav--item{
  font-family: 'lineto-akkurat-bold';
}

h2{
  font-family: 'lineto-akkurat-bold';
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 0.5px;
  color: #7F8E95;
  padding-bottom: 20px;
  display: block;

}
h3{
  font-family: 'lineto-akkurat-light';
  font-size: 2.5rem;
  font-weight: normal;
  letter-spacing: 0.1rem;
  color: #fff;
  display: block;
}

h4{
color: #212121;
text-transform: uppercase;
font-family: 'lineto-akkurat-regular';
font-size: 1rem;
letter-spacing: 1.3px;
padding: 10px 0;

}
.darkTypo{
  color: #212121;
}
.mediumTypo{
  font-size: 1.75rem;
}

.textAlign{
  text-align: center;
}
